@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap);
body, html {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 25px 0;
}

body {
    background: rgb(82,82,82);
    background: linear-gradient(148deg, rgba(82,82,82,1) 0%, rgba(42,42,42,1) 100%);
    background-repeat: no-repeat;
    background-attachment: fixed;
}

h1, h2, h3, h4, h5, h6, input {
    color: lightgray;
    background-color: transparent;
    font-weight: 400;
    font-family: 'Roboto', sans-serif;
}

input:focus {
    background-color: rgba(42,42,42,1);
}
.notebook {
    width: 90%;
    margin: 0 auto;
    padding: 1rem;
    background: rgb(90, 90, 90);
}

.notebook-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    background: rgb(60, 60, 60);
}

.notebook-body {
    list-style: none;
    margin: 0;
    padding: 0;
    padding-top: 1rem;
}
.task_list {
    background: rgb(60, 60, 60);
    margin: .5rem 0;
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    grid-template-rows: 3rem;
    grid-template-areas: 
        'title title title date date date status status' 
        'bar bar bar bar bar bar percent expand' 
        'tasks tasks tasks tasks tasks tasks tasks tasks';
}

#title {
    grid-area: title;
    border: 0;
    margin: 6px;
    align-self: center;
    text-align: left;
}

#date {
    grid-area: date;
    margin: 6px;
    align-self: center;
    text-align: right;
}

#status {
    grid-area: status;
    margin: 6px;
    border: 1px solid white;
    border-radius: 5px;
    align-self: center;
    text-align: center;
}

#progress {
    grid-area: bar;
    margin: 6px;
    border: 1px solid darkgreen;
    border-radius: 5px;
}

#progress-value {
    border-radius: 5px;
    background: linear-gradient(148deg, rgba(0,111,5,1) 0%, rgba(0,207,0,1) 100%);
}

#percent {
    grid-area: percent;
    margin: 6px;
    align-self: center;
    text-align: left;
}

#expand {
    grid-area: expand;
    margin: 6px;
    align-self: center;
    background-image: linear-gradient(148deg, rgba(82,82,82,1) 0%, rgba(42,42,42,1) 100%);
}

#tasks {
    grid-area: tasks;
    margin: 6px;
    align-self: center;
}
.task {
    margin: 6px;
    display: flex;
    border: 2px solid rgb(66, 66, 66);
    flex-direction: row;
    justify-content: space-between;
    align-content: space-between;
}

.title {
    grid-area: title;
    border: 0;
    margin: 6px;
    align-self: center;
    text-align: left;
}
